.score {
  position: fixed;
  background: #252525;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.8), 0 0 50px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  padding: 20px;
  font-size: 25px;
  font-family: monospace;
}

.score.now {
  top: 10px;
  right: 10px;
}

.score.best {
    bottom: 10px;
    left: 10px;
}
